import React, { Component, useState } from 'react'
import axios from 'axios'
import { Container, Grid, Typography, Toolbar, TextField, Button, Input, Checkbox} from '@mui/material'
import { Link } from 'react-router-dom'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import TuneIcon from '@mui/icons-material/Tune'
import CloudUploadIcon from '@mui/icons-material/CloudUpload'

import '../../styles/Main.css'

export default class SendNotices extends Component {

  render() {


    return (
      <main>
        <Container maxWidth="lg">
            <Grid container>
                <Grid item lg={12} md={12} xs={12}>
                    <div className="Main-root">
                        <div className="High-root">
                            <Toolbar>
                                <Link to="http://localhost:3000/notices"><ArrowBackIcon color="warning" fontSize="large" /></Link>
                                <Typography variant="h3" textAlign="center"> </Typography>
                            </Toolbar>
                        </div>
                        <div className="Middle-full-root">
                            <div className="Text-root">
                              <Grid container>
                                <Grid item lg={12} md={12} xs={12}>
                                  <Typography> </Typography>
                                  <TextField fullWidth id="outlined-basic" label=" " variant="outlined"/>
                                </Grid>
                              </Grid>
                            </div>

                            <div className="Media-root">
                              <Grid container>
                                <Grid item lg={12} md={12} xs={12}>
                                  <Typography></Typography>
                                  <Input type="file" />
                                </Grid>
                              </Grid>
                            </div>

                            <div className="Date-root">
                              <Grid container>
                                <Grid item lg={12} md={12} xs={12}>
                                  <Typography> </Typography>
                                </Grid>
                              </Grid>
                            </div>

                            <div className="Source-root">
                              <Grid container>
                                <Grid item lg={12} md={12} xs={12}>
                                  <Typography></Typography>
                                  <Grid container>
                                    <Grid item><Typography><Checkbox/>  </Typography></Grid>
                                    <Grid item><Typography><Checkbox/> </Typography></Grid>
                                    <Grid item><Typography><Checkbox/></Typography></Grid>
                                  </Grid>
                                </Grid>
                              </Grid>
                            </div>

                            <div className="Society-root">
                              <Grid container>
                                <Grid item lg={12} md={12} xs={12}>
                                  <Typography> </Typography>
                                  <Grid container>
                                    <Grid item pr={3}><Typography><Checkbox/>,    1    </Typography></Grid>
                                    <Grid item pr={3}><Typography><Checkbox/>,    1    </Typography></Grid>
                                  </Grid>
                                  <Grid container>
                                    <Grid item lg={2}><Typography><Checkbox/></Typography></Grid>
                                    <Grid item lg={10}><Typography><TextField label=" UID"/> ,    1    </Typography></Grid>
                                  </Grid>
                                </Grid>
                              </Grid>
                            </div>

                            <div className="Low-root">
                              <Grid container mt={3} mb={4} spacing={3} justifyContent="center">
                                <Grid item>
                                  <Button variant="contained" color="warning"> </Button>
                                </Grid>
                                <Grid item>
                                  <Button variant="contained" color="inherit"></Button>
                                </Grid>
                              </Grid>
                            </div>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </Container>
      </main>
    )
  }
}
